import React, { Component } from 'react'
import wrapComponent from '../../store';
import Nav from "./Nav"

const HOME_PAGE = "#";

class Home extends Component {

  constructor(props) {
    super(props);
    this.state = { isVisible: true };
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.id !== this.props.id) {
      this.setState({isVisible: (nextProps.id===nextProps.slideId?true:false)});
    }
  }

  render() {
    const { isVisible } = this.state;
    return (
      <div id="home" className={(isVisible ? '' : 'home-hidden')}>
        <div className="home-wrapper">
          <div className="glitch-image">
            <div className="glitch__img"></div>
            <div className="glitch__img"></div>
            <div className="glitch__img"></div>
            <div className="glitch__img"></div>
          </div>
          <div className="home-content home-photo main-photo dark">
            <div className="text-center vertical-center">
              <span className="home-title" data-text="Lâm Thành">Lâm Thành</span>
              <span className="home-title glitch" style={{fontWeight: 'bold'}} data-text="Lộc">Lộc</span>
              <span className="home-caption"><strong>Developer</strong></span>
            </div>
          </div>
          <a href={HOME_PAGE} id="btn-start" onClick={
            () => this.props.NextSlide()
          }>
          </a>  
          <Nav title={['Lâm Thành ',<strong>Lộc</strong>]}/>         
        </div>
      </div> 
    )
  }
}

export default wrapComponent(Home);