import React, { Component } from 'react'
import wrapComponent from '../../store'
import AboutMe from './AboutMe'
import Education from './Education'
import Hobbies from './Hobbies'
import Language from './Language'
import Skills from './Skills'
import posed from 'react-pose';

const ResumeGroup = posed.div({
    enter: {
      delayChildren: 200,
      staggerChildren: 200
    },
    exit: {
    }
});

const ResumeCol = posed.div({
  enter: {
    y: '0',
    opacity: 1,
    transition: {
      duration: 600,
      opacity: {
        duration: 1000
      }
    }
  },
  exit: {
    opacity: 0,
    y: '100%'
  },
});

const TITLE = "My Resume";
const CAPTION = `Check my <span>skills</span> &amp; <span>abilities</span>`;

class Resume extends Component {
    
    constructor(props) {
        super(props);
        this.state = {
          isVisible: false,
        };
      }

    componentWillReceiveProps(nextProps) {
        if (nextProps.id !== this.props.id) {
            if(nextProps.id+1 === this.props.slideId){
                this.props.UpdateTitle(this.props.prevTitle,TITLE);
                this.setState({
                    isVisible: false,
                });
            }
            if(nextProps.id-1 === this.props.slideId){
                this.props.UpdateTitle(TITLE,this.props.nextTitle);
                this.setState({
                    isVisible: false,
                });
            }
            if(nextProps.id === this.props.slideId){
                this.setState({
                    isVisible: true,
                });
                this.props.UpdateCaption(CAPTION);
            }
        }
    }

  render() {
    return (
        <section id="resume" className="swiper-slide">
            <div className="section-wrapper">
                <div className="resume-grid padded">
                    <ResumeGroup pose={this.state.isVisible?'enter':'exit'} className="row">
                        <ResumeCol className="resume-col">
                            <AboutMe NextSlide={this.props.NextSlide}/>
                        </ResumeCol>
                        <ResumeCol className="resume-col">
                            <Skills isVisible={this.state.isVisible} />
                            <Education isVisible={this.state.isVisible}/>
                        </ResumeCol>
                        <ResumeCol className="resume-col">
                            <Language isVisible={this.state.isVisible}/>
                            <Hobbies/>
                        </ResumeCol>
                    </ResumeGroup>
                </div>
            </div>
        </section>
    )
  }
}

export default wrapComponent(Resume);