import React, { Component } from 'react'
import posed from 'react-pose';
import Fade from 'react-reveal';

const CircleChart = posed.circle({
  show: {
    strokeDasharray: ({ x }) => `${x} ${100-x}`,
    delay: 1200,
    transition: {
      duration: 1000,
    },
  },
  hide: {
    strokeDasharray: '0 100'
  },
});

const Chart = (props)=>{
  return (
    <div className="single-chart font-alt">
      <svg viewBox="0 0 40 40" class="chart">
        <circle className="background-chart" r="15.91549430918954" cx="20" cy="20"></circle>
        <CircleChart
          pose={props.isVisible?'show':'hide'}
          x={props.percent}
          className="main-chart" r="15.91549430918954" cx="20" cy="20"></CircleChart>
        <text y="55%">
          <tspan x="50%" text-anchor="middle" className="percent">{props.percent}%</tspan>   
        </text>
      </svg>
      <Fade bottom forever={true} cascade when={props.isVisible}>
        <div className="chart-content text-uppercase">	
            <span className="chart-title">{props.title}</span>
            <span className="chart-caption text-muted">{props.level}</span>
        </div>
      </Fade>
    </div>
  );
}

export default class Language extends Component {
  render() {
    const isVisible = this.props.isVisible; 
    return(
      <div className="resume-box">
        <h4 className="title-bar title-bar-black title-bar-hover"><strong>Language</strong> Skills</h4>
        <div className="box-content text-center">
          <Chart title="English" percent={50} isVisible={isVisible} level="High intermediate level"/>
        </div>
    </div>
    )
  }
};