import React, { Component } from 'react'
import posed from 'react-pose';

const SkillBarDiv = posed.div({
  show: {
    width: ({ x }) => `${x}%`,
    delay: 1100,
    transition: {
      duration: 1000,
    },
  },
  hide: {
    width:'0%',
  },
});

const SkillBar = (props)=>{
  const [current, setCurrent] = React.useState(0);
  const onValueChange = React.useMemo(
    () => ({ width: p => setCurrent(Math.floor(parseFloat(p)))})
  );
  return (
    <div className="skill-bar">
        <div className="progress">
          <SkillBarDiv
            pose={props.isVisible?'show':'hide'} x={props.value} 
            onValueChange={onValueChange}
            className="progress-bar progress-bar-black"
            role="progressbar"
            aria-valuenow={props.value}
            aria-valuemin="0"
            aria-valuemax="100">
              <span className="percent">{current}%</span>
          </SkillBarDiv>
        </div>
        <h6 className="skill-title">{props.title}</h6>
    </div>
  );
}

export default class Skills extends Component {
  render() {
    const isVisible = this.props.isVisible; 
    return(
      <div className={"resume-box skills"}>
        <h4 className="title-bar title-bar-black title-bar-hover"><strong>Skills</strong> &amp; Abilities</h4>
        <div className="box-content">
          <SkillBar title="Adobe Photoshop & Lightroom" value={80} isVisible={isVisible} />
          <SkillBar title="C/C++" value={60} isVisible={isVisible} />
          <SkillBar title="PHP" value={80} isVisible={isVisible} />
          <SkillBar title="NodeJS/Javascript" value={50} isVisible={isVisible} />
          <SkillBar title="HTML/CSS/Jquery" value={80} isVisible={isVisible} />
          <SkillBar title="SQL & NoSQL" value={70} isVisible={isVisible} />
        </div>
    </div>

    )
  }
};